'use strict';

/*
* When adding modules to the namespace, ensure that they are added in alphabetical order according to module name.
*/

// MODULES //

import setReadOnly from '@stdlib/utils/define-read-only-property';


// MAIN //

/**
* Top-level namespace.
*
* @namespace ns
*/
var ns = {};

/**
* @name ARCH
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/os/arch}
*/
import ARCH from './../arch';
setReadOnly( ns, 'ARCH', ARCH );

/**
* @name BYTE_ORDER
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/os/byte-order}
*/
import BYTE_ORDER from './../byte-order';
setReadOnly( ns, 'BYTE_ORDER', BYTE_ORDER );

/**
* @name configdir
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/os/configdir}
*/
import configdir from './../configdir';
setReadOnly( ns, 'configdir', configdir );

/**
* @name FLOAT_WORD_ORDER
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/os/float-word-order}
*/
import FLOAT_WORD_ORDER from './../float-word-order';
setReadOnly( ns, 'FLOAT_WORD_ORDER', FLOAT_WORD_ORDER );

/**
* @name homedir
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/os/homedir}
*/
import homedir from './../homedir';
setReadOnly( ns, 'homedir', homedir );

/**
* @name NUM_CPUS
* @memberof ns
* @readonly
* @type {PositiveInteger}
* @see {@link module:@stdlib/os/num-cpus}
*/
import NUM_CPUS from './../num-cpus';
setReadOnly( ns, 'NUM_CPUS', NUM_CPUS );

/**
* @name PLATFORM
* @memberof ns
* @readonly
* @type {string}
* @see {@link module:@stdlib/os/platform}
*/
import PLATFORM from './../platform';
setReadOnly( ns, 'PLATFORM', PLATFORM );

/**
* @name tmpdir
* @memberof ns
* @readonly
* @type {Function}
* @see {@link module:@stdlib/os/tmpdir}
*/
import tmpdir from './../tmpdir';
setReadOnly( ns, 'tmpdir', tmpdir );


// EXPORTS //

export default ns;
