'use strict';

// MODULES //

import nav from './navigator.js';


// VARIABLES //

var n;


// FUNCTIONS //

/**
* Returns the number of CPUs.
*
* @private
* @returns {PositiveInteger} number of CPUs
*
* @example
* var n = numCPUs();
* // returns <number>
*/
function numCPUs() {
	return nav.hardwareConcurrency || 1;
}


// MAIN //

n = numCPUs();


// EXPORTS //

export default n;
